import React, { useState } from 'react';
import { useAppSelector } from '../../hooks/reduxHooks';
import { useFirebaseLists } from '../../hooks/useFirebaseLists';
import { useUserAvatar } from '../../hooks/useUserAvatar';
import {
    acceptFriendRequest,
    rejectFriendRequest,
    blockUser,
    unblockUser,
    hideGroup,
    unhideGroup
} from '../../services/friendService';
import { socketService } from '../../services/socketService';
import ConfirmModal from '../common/ConfirmModal'; // Import Modal mi

// --- 1. COMPONENT CARD (Gi nguyn) ---
const UserCard = ({ username, subText, actions }: { username: string, subText?: string, actions: React.ReactNode }) => {
    const avatar = useUserAvatar(username);
    return (
        <div className="flex items-center justify-between p-4 bg-white dark:bg-gray-800 border border-gray-100 dark:border-gray-700 rounded-2xl shadow-sm hover:shadow-md transition-all">
            <div className="flex items-center gap-4">
                <img src={avatar} alt={username} className="w-12 h-12 rounded-full object-cover border border-gray-200 dark:border-gray-600" />
                <div>
                    <h3 className="font-bold text-gray-800 dark:text-white text-base">{username}</h3>
                    {subText && <p className="text-xs text-gray-500">{subText}</p>}
                </div>
            </div>
            <div className="flex items-center gap-2">{actions}</div>
        </div>
    );
};

// --- 2. COMPONENT CHNH ---
const ContactWindow = () => {
    const currentUser = useAppSelector((state) => state.auth.user?.username);
    const activeTab = useAppSelector((state) => state.ui.activeContactTab);
    const { friends, groups, friendRequests, groupInvites, blocks, hiddenGroups } = useFirebaseLists(currentUser);

    // --- STATE QUN L MODAL ---
    const [modalConfig, setModalConfig] = useState<{
        isOpen: boolean;
        title: string;
        message: string;
        isDanger?: boolean;
        onConfirm: () => void;
    }>({
        isOpen: false, title: '', message: '', isDanger: false, onConfirm: () => {}
    });

    const closeModal = () => setModalConfig({ ...modalConfig, isOpen: false });

    // --- HM HELPER  M MODAL ---
    const openConfirm = (title: string, message: string, action: () => void, isDanger = false) => {
        setModalConfig({
            isOpen: true,
            title,
            message,
            isDanger,
            onConfirm: async () => {
                await action();
                closeModal();
            }
        });
    };

    // --- CC HM X L HNH NG (Dng openConfirm thay v window.confirm) ---

    const handleAcceptFriend = async (target: string) => {
        if (!currentUser) return;
        try {
            await acceptFriendRequest(currentUser, target);
            socketService.sendMessageToPeople(target, "oke ban");
        } catch (error: any) {
            // C th dng Modal thng bo li  y thay alert nu mun xn hn
            alert(error.message);
        }
    };

    const handleRejectFriend = (target: string) => {
        if (!currentUser) return;
        openConfirm(
            "T chi kt bn?",
            `Bn c chc chn mun xa li mi t ${target}?`,
            () => rejectFriendRequest(currentUser, target),
            true // Danger action
        );
    };

    const handleBlock = (target: string) => {
        if (!currentUser) return;
        openConfirm(
            "Chn ngi dng?",
            `Bn c chc chn mun chn ${target}? H s khng th nhn tin cho bn na.`,
            () => blockUser(currentUser, target),
            true
        );
    };

    const handleUnblock = (target: string) => {
        if (!currentUser) return;
        openConfirm(
            "B chn?",
            `Cho php ${target} lin lc li vi bn?`,
            () => unblockUser(currentUser, target)
        );
    };

    const handleHideGroup = (groupName: string) => {
        if (!currentUser) return;
        openConfirm(
            "n nhm chat?",
            `Nhm ${groupName} s b n khi danh sch chat chnh. Bn c th tm li trong mc "Nhm  n".`,
            () => hideGroup(currentUser, groupName)
        );
    };

    const handleUnhideGroup = async (groupName: string) => {
        if (!currentUser) return;
        try {
            await unhideGroup(currentUser, groupName);
        } catch (error: any) {
            alert(error.message);
        }
    };

    // --- RENDER NI DUNG (Gi nguyn logic c, ch gi hm handle...) ---
    const renderContent = () => {
        switch (activeTab) {
            case 'friends':
                if (friends.length === 0) return <EmptyState text="Cha c bn b no." />;
                return (
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
                        {friends.map(friend => (
                            <UserCard key={friend} username={friend} subText="Bn b"
                                      actions={
                                          <button onClick={() => handleBlock(friend)} className="px-4 py-2 bg-red-50 text-red-600 hover:bg-red-100 rounded-xl text-sm font-semibold transition-colors">Chn</button>
                                      }
                            />
                        ))}
                    </div>
                );

            case 'groups':
                if (groups.length === 0) return <EmptyState text="Cha tham gia nhm no." />;
                return (
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
                        {groups.map(group => (
                            <UserCard key={group} username={group} subText="Nhm chat"
                                      actions={
                                          <button onClick={() => handleHideGroup(group)} className="px-4 py-2 bg-gray-100 text-gray-600 hover:bg-gray-200 dark:bg-gray-700 dark:text-gray-300 rounded-xl text-sm font-semibold transition-colors flex items-center gap-2">
                                              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21" /></svg> n
                                          </button>
                                      }
                            />
                        ))}
                    </div>
                );

            case 'friendRequests':
                if (friendRequests.length === 0) return <EmptyState text="Khng c li mi kt bn no." />;
                return (
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
                        {friendRequests.map(req => (
                            <UserCard key={req} username={req} subText="Mun kt bn vi bn"
                                      actions={
                                          <>
                                              <button onClick={() => handleRejectFriend(req)} className="px-3 py-2 text-gray-500 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-gray-700 rounded-xl text-sm font-semibold transition-colors">Xa</button>
                                              <button onClick={() => handleAcceptFriend(req)} className="px-4 py-2 bg-blue-600 text-white hover:bg-blue-700 rounded-xl text-sm font-semibold shadow-md shadow-blue-500/20 transition-colors">Xc nhn</button>
                                          </>
                                      }
                            />
                        ))}
                    </div>
                );

            case 'hiddenGroups':
                if (hiddenGroups.length === 0) return <EmptyState text="Khng c nhm no b n." />;
                return (
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
                        {hiddenGroups.map(group => (
                            <UserCard key={group} username={group} subText="ang b n"
                                      actions={
                                          <button onClick={() => handleUnhideGroup(group)} className="px-4 py-2 bg-green-50 text-green-600 hover:bg-green-100 rounded-xl text-sm font-semibold transition-colors">Hin li</button>
                                      }
                            />
                        ))}
                    </div>
                );

            case 'blocks':
                if (blocks.length === 0) return <EmptyState text="Bn cha chn ai c." />;
                return (
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
                        {blocks.map(blockedUser => (
                            <UserCard key={blockedUser} username={blockedUser} subText=" chn"
                                      actions={
                                          <button onClick={() => handleUnblock(blockedUser)} className="px-4 py-2 bg-blue-50 text-blue-600 hover:bg-blue-100 rounded-xl text-sm font-semibold transition-colors">B chn</button>
                                      }
                            />
                        ))}
                    </div>
                );

            case 'groupInvites':
                return <EmptyState text="Tnh nng ang pht trin..." />;

            default:
                return null;
        }
    };

    const getTitle = () => {
        switch(activeTab) {
            case 'friends': return 'Danh sch bn b';
            case 'groups': return 'Nhm & Cng ng';
            case 'friendRequests': return 'Li mi kt bn';
            case 'hiddenGroups': return 'Kho lu tr nhm';
            case 'blocks': return 'Danh sch chn';
            case 'groupInvites': return 'Li mi vo nhm';
            default: return 'Danh b';
        }
    }

    return (
        <div className="flex flex-col h-full w-full bg-gray-50 dark:bg-gray-900 relative">
            <div className="px-8 py-6 bg-white dark:bg-gray-900 border-b border-gray-200 dark:border-gray-800">
                <h1 className="text-2xl font-bold text-gray-800 dark:text-white">{getTitle()}</h1>
                <p className="text-sm text-gray-500 mt-1">Qun l cc mi quan h ca bn</p>
            </div>

            <div className="flex-1 overflow-y-auto p-8">
                {renderContent()}
            </div>

            {/* --- RENDER MODAL --- */}
            <ConfirmModal
                isOpen={modalConfig.isOpen}
                title={modalConfig.title}
                message={modalConfig.message}
                isDanger={modalConfig.isDanger}
                onConfirm={modalConfig.onConfirm}
                onCancel={closeModal}
            />
        </div>
    );
};

const EmptyState = ({ text }: { text: string }) => (
    <div className="flex flex-col items-center justify-center h-full text-gray-400 mt-10">
        <div className="w-20 h-20 bg-gray-100 dark:bg-gray-800 rounded-full flex items-center justify-center mb-4">
            <svg className="w-10 h-10" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" /></svg>
        </div>
        <p>{text}</p>
    </div>
);

export default ContactWindow;