import { useEffect, useRef, useState, useCallback } from 'react';
import { socketService } from '../services/socketService';
import { useAppSelector } from './reduxHooks';


const ICE_SERVERS = {
   iceServers: [
       { urls: 'stun:stun.l.google.com:19302' },
       { urls: 'stun:global.stun.twilio.com:3478' }
   ],
};


export const useWebRTC = (currentPartner: string) => {
   const [localStream, setLocalStream] = useState<MediaStream | null>(null);
   const [remoteStream, setRemoteStream] = useState<MediaStream | null>(null);
   const [isCalling, setIsCalling] = useState(false);
   const [isIncoming, setIsIncoming] = useState(false);
   const [callStatus, setCallStatus] = useState<string>('');


   const peerConnection = useRef<RTCPeerConnection | null>(null);
   const { user } = useAppSelector(state => state.auth);
   const chatType = useAppSelector(state => state.currentChat.type) || 'people';

   const candidateQueue = useRef<RTCIceCandidate[]>([]);
   const sendingTimeout = useRef<NodeJS.Timeout | null>(null);


   const createPeerConnection = useCallback(() => {
       if (peerConnection.current) return peerConnection.current;


       const pc = new RTCPeerConnection(ICE_SERVERS);


       pc.onicecandidate = (event) => {
           if (event.candidate) {
            candidateQueue.current.push(event.candidate);
            if (sendingTimeout.current) clearTimeout(sendingTimeout.current);
            sendingTimeout.current = setTimeout(() => {
                if (candidateQueue.current.length > 0) {
                    candidateQueue.current.forEach(cand => {
                        socketService.sendWebRTCSignal(currentPartner, {
                            type: 'CANDIDATE',
                            candidate: cand
                        }, chatType);
                    });
                    candidateQueue.current = []; 
                }
            }, 100);
           }
       };


       pc.ontrack = (event) => {
           console.log("Nhn remote stream");
           setRemoteStream(event.streams[0]);
       };
      
       pc.onconnectionstatechange = () => {
            switch(pc.connectionState) {
                case 'connected': setCallStatus('connected'); break;
                case 'disconnected':
                case 'failed':
                case 'closed':
                   endCall();
                   break;
            }
       };


       peerConnection.current = pc;
       return pc;
   }, [currentPartner,chatType]);


   // 2. Lng nghe tn hiu t SocketService
   useEffect(() => {
       socketService.registerWebRTCListener(async (sender, payload,target) => {
           
            if (chatType === 'people') {
                if (sender !== currentPartner) return;
            } else if (chatType === 'room') {
                if (target !== currentPartner) return;
            }


           if (!peerConnection.current && payload.type !== 'OFFER') return;


           const pc = peerConnection.current || createPeerConnection();


           switch (payload.type) {
               case 'OFFER':
                   setIsIncoming(true);
                   setCallStatus('incoming');
                   // Lu offer vo remote description tm thi (hoc x l sau khi bm nghe)
                   await pc.setRemoteDescription(new RTCSessionDescription(payload.offer));
                   break;


               case 'ANSWER':
                   await pc.setRemoteDescription(new RTCSessionDescription(payload.answer));
                   break;


               case 'CANDIDATE':
                   try {
                       if (payload.candidate) {
                           await pc.addIceCandidate(new RTCIceCandidate(payload.candidate));
                       }
                   } catch (e) {
                       console.error("Li add ICE:", e);
                   }
                   break;
              
               case 'END_CALL':
                   endCall();
                   break;
           }
       });


       return () => {
       };
   }, [currentPartner, createPeerConnection,chatType]);




   const startLocalStream = async () => {
       try {
           const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
           setLocalStream(stream);
           return stream;
       } catch (err) {
           console.error("Khng th ly camera:", err);
           alert("Vui lng cp quyn Camera/Micro  gi video!");
           return null;
       }
   };


   const startCall = async () => {
       const stream = await startLocalStream();
       if (!stream) return;


       setIsCalling(true);
       setCallStatus('calling');


       const pc = createPeerConnection();
       // Add tracks
       stream.getTracks().forEach(track => pc.addTrack(track, stream));


       // To Offer
       const offer = await pc.createOffer();
       await pc.setLocalDescription(offer);


       // Gi Offer
       socketService.sendWebRTCSignal(currentPartner, {
           type: 'OFFER',
           offer: offer
       },chatType);
   };


   const answerCall = async () => {
       const stream = await startLocalStream();
       if (!stream) return;


       setIsIncoming(false);
       setIsCalling(true);
       setCallStatus('connected');


       const pc = peerConnection.current;
       if (!pc) return;


       // Add tracks
       stream.getTracks().forEach(track => pc.addTrack(track, stream));


       // To Answer
       const answer = await pc.createAnswer();
       await pc.setLocalDescription(answer);


       // Gi Answer
       socketService.sendWebRTCSignal(currentPartner, {
           type: 'ANSWER',
           answer: answer
       },chatType);
   };


   const endCall = () => {
       localStream?.getTracks().forEach(track => track.stop());
      
       if (peerConnection.current) {
           peerConnection.current.close();
           peerConnection.current = null;
       }


       setLocalStream(null);
       setRemoteStream(null);
       setIsCalling(false);
       setIsIncoming(false);
       setCallStatus('');


       // Gi tn hiu kt thc cho bn kia
       socketService.sendWebRTCSignal(currentPartner, { type: 'END_CALL' },chatType);
   };


   return {
       localStream,
       remoteStream,
       isCalling,
       isIncoming,
       callStatus,
       startCall,
       answerCall,
       endCall
   };
};
