import { createSlice, PayloadAction } from "@reduxjs/toolkit";

interface SearchState {
    isSearching: boolean;           // ang ch server tr li?
    searchResult: boolean | null;   // true: Tn ti, false: Khng tn ti, null: Cha tm
    searchedUsername: string;       // Lu li tn va tm  UI so snh
}

const initialState: SearchState = {
    isSearching: false,
    searchResult: null,
    searchedUsername: '',
};

const searchSlice = createSlice({
    name: "search",
    initialState,
    reducers: {
        // Gi khi bt u nhn Enter
        startSearching: (state, action: PayloadAction<string>) => {
            state.isSearching = true;
            state.searchResult = null; // Reset kt qu c
            state.searchedUsername = action.payload;
        },
        // Gi khi Socket nhn c phn hi
        setSearchResult: (state, action: PayloadAction<boolean>) => {
            state.isSearching = false;
            state.searchResult = action.payload;
        },
        // Reset khi xa  search
        clearSearch: (state) => {
            state.isSearching = false;
            state.searchResult = null;
            state.searchedUsername = '';
        },
    },
});

export const { startSearching, setSearchResult, clearSearch } = searchSlice.actions;
export default searchSlice.reducer;