export function replaceEmojiShortcodes(text: string): string {
  let result = text;
  Object.entries(EMOJI_MAP).forEach(([code, emoji]) => {
    result = result.replaceAll(code, emoji);
  });
  return result;
}

// Ly danh sch emoji v encode ca n
export const EMOJI_LIST = Object.entries(EMOJI_MAP).map(([code, emoji]) => ({
  code,
  emoji,
}));
